.todos-container {
  width: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}
.todos-container .header {
  width: 60%;
  height: 48px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
}
.todos-container .header .ant-input {
  width: 87%;
  height: 48px;
  margin-right: 3%;
  font-size: 24px;
}
.todos-container .header .btn {
  width: 10%;
  height: 32px;
}
.todos-container .center {
  width: 60%;
  display: flex;
}
.todos-container .center .ml {
  width: 17%;
  margin-left: 3%;
}
.todos-container .center .left {
  font-weight: bold;
  color: #679ACC;
}
.todos-container .center .all,
.todos-container .center .active,
.todos-container .center .completed {
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  border-radius: 50% 50%;
}
.todos-container .center .clear:hover {
  color: #679ACC;
  cursor: pointer;
}
.todos-container .list {
  width: 60%;
  margin-top: 30px;
  font-size: 16px;
}
.todos-container .list .item {
  display: flex;
  margin-top: 16px;
}
.todos-container .list .item .block {
  width: 90%;
  margin: 0 16px;
}
.todos-container .list .item .time {
  width: 100%;
  text-align: right;
  font-size: 10px;
  margin-top: 4px;
  margin-bottom: 4px;
}
